.b-booking-group {
  padding-top: 82px;
  padding-bottom: 75px;

  .form-label {
    margin-bottom: 18px;
  }
  .form-group {
    margin-bottom: 36px;
  }
}

.b-booking-nav {
  padding-right: 80px;
  &__item {
    position: relative;
    display: block;
    margin-bottom: 15px;
    padding-bottom: 10px;
    background-color: #f2f6f7;
    &:hover {
      text-decoration: none;
    }
    .ic {
      position: absolute;
      top: 34px;
      left: 28px;
      font-size: 18px;
      color: #999;
    }

    &.active {
      .ic {
        color: #fff;
      }
    }

    &.active {
      .b-booking-nav {
        &__info {
          color: #fff;
        }
        &__main {
          margin-right: -10px;
          border-right-color: transparent;
          &:before {
            opacity: 1;
          }
          &:after {
            border-color: #fff transparent transparent #fff;
          }
        }
      }
    }
  }
  &__header {
    padding: 26px 27px 14px;
    font-size: 18px;
    color: #94b9c1;
    text-transform: uppercase;
  }
  &__main {
    position: relative;
    padding: 28px 20px 35px 60px;
    border-right: 3px solid;
    background-color: #444;
    &:after {
      position: absolute;
      top: 4px;
      left: 4px;
      border-width: 6px;
      border-style: solid;
      border-color: rgba(255,255,255,.15) transparent transparent rgba(255,255,255,.15);
      content: '';
    }
    &:before {
      position: absolute;
      z-index: 10;
      top: 0;
      left: 100%;
      margin-left: 3px;
      opacity: 0;
      border-top: 53px solid transparent;
      border-bottom: 53px solid transparent;
      border-left: 30px solid;
      content: '';
    }
  }
  &__title {
    display: block;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 700;
    color: #fff;
  }
  &__info {
    display: block;
    line-height: 1;
    color: #999;
  }
}


.b-booking-main {
  padding-left: 30px;
  &__section {
    margin-bottom: 65px;
  }
  &__title {
    margin-bottom: 45px;
    font: 600 30px/1 Montserrat;
    color: #222;
  }
  &__cat {
    margin-top: 25px;
  }
  & &__cat-item {
    margin-bottom: 35px;
  }
  &__cat-item {
    position: relative;
    width: 200px;
    margin-right: 5px;
    margin-left: 5px;
    padding: 7px 20px 19px;
    font: 600 11px/1 Montserrat;
    color: #222;
    border: 2px solid #e4edef;
    border-radius: 2px;
    transition: all .3s;
    text-align: center;
    text-transform: uppercase;
    input {
      display: none;
    }
    .ic {
      display: block;
      margin-right: 0;
      font-size: 70px;
      font-weight: 400;
      line-height: .8;
      color: #ccc;
      transition: all .3s;
    }
    .ic-check {
      position: absolute;
      top: 18px;
      right: 15px;
      font-size: 18px;
      color: #fff;
      color: #fff;
    }
    &.active,
    &:hover {
      color: #fff;
      .ic {
        opacity: 1;
        color: #fff;
      }
    }
  }
}


.b-booking-price {
  margin-bottom: 42px;
  padding-bottom: 37px;
  padding-left: 10px;
  border-bottom: 1px solid #ddd;
  &__title {
    margin-bottom: 7px;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 700;
    color: #222;
  }
  &__day {
    margin-left: 15px;
    font-family: Montserrat;
    font-size: 14px;
    color: #666;
  }
  &__inner {
    padding-right: 110px;
  }
  &__info {
    line-height: 2;
  }
  &__price {
    margin-right: 10px;
    font-size: 60px;
    font-weight: 700;
    line-height: 1;
  }
  &__price-up {
    display: inline-block;
    font-size: 33px;
    vertical-align: super;
  }
  &__label {
    display: inline-block;
    margin-left: 80px;
    .ic {
      margin-right: 10px;
    }
  }
  .custom-control-label {
    margin-left: -7px;
    padding-left: 20px;
  }

  &_sm {
    margin-bottom: 32px;
    padding-bottom: 19px;
  }
  &_sm & {
    &__title {
      margin-bottom: -3px;
    }
  }
}


.b-booking-total {
  position: relative;
  margin-bottom: 30px;
  padding: 31px 40px 34px;
  border: 2px solid;
  .ic {
    position: absolute;
    right: 20px;
    bottom: 20px;
    font-size: 48px;
    color: #bfd8de;
  }

  &__title {
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    color: #222;
    text-transform: uppercase;
  }
}
