.b-main-slider {
  font-family: Montserrat;
  color: #fff;
  &__slide-1 {
    padding-top: 340px;
  }
  &__slogan {
    font-size: 16px;
    line-height: 1;
    letter-spacing: 14.08px;
  }
  &__slogan_2 {
    padding-top: 290px;
    letter-spacing: 10.88px;
    text-transform: uppercase;
  }
  &__title-wrap {
    display: inline-block;
    margin-left: -30px;
    text-align: center;
  }
  &__title {
    font-size: 110px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: -2.75px;
    text-transform: uppercase;
  }
  &__title_2 {
    text-transform: none;
  }
  &__subtitle {
    margin-top: -235px;
    font-size: 170px;
    font-weight: 700;
    text-align: right;
    letter-spacing: -4.25px;
    text-transform: uppercase;
  }
  &__wrap-figure {
    position: relative;
    z-index: 1;
  }
  &__figure-1 {
    margin-top: -200px;
    margin-left: 25px;
  }
  &__label {
    position: relative;
    display: inline-block;
    margin-top: 12px;
    margin-bottom: 35px;
    font-size: 24px;
    letter-spacing: -.6px;
    &:after {
      display: block;
      height: 1px;
      margin-top: -5px;
      background: #fff;
      content: '';
      @supports (clip-path: polygon(0 0)) {
        height: 6px;

        clip-path: polygon(0 100%, 100% 0, 100% 100%);
      }
    }
  }

  &_mod-a {
    .b-main-slider__slide-1 {
      padding-top: 300px;
    }
    .b-main-slider__slogan_2 {
      padding-top: 276px;
    }
  }
}

.b-main-slider-price {
  height: 272px;
  margin-top: -30px;
  background: url('../media/content/b-main-slider/bg-price.png') no-repeat 100% 50%;
  &__wrap {
    position: relative;
    z-index: 2;
  }
  &__title {
    font-size: 18px;
    letter-spacing: -.45px;
  }
  &__main {
    display: inline-block;
    margin-top: -7px;
  }
  &__inner {
    width: 220px;
    height: 220px;
    margin-right: 26px;
    margin-left: auto;
    padding-top: 48px;
    text-align: center;
  }
  &__value {
    font-size: 42px;
    font-weight: 700;
    line-height: 1;
    vertical-align: 60%;
  }
  &__number {
    margin-top: -5px;
    font-size: 72px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: -1.8px;
  }
  &__note {
    margin-top: 5px;
    font-size: 12px;
    letter-spacing: 1.2px;
    text-transform: uppercase;
  }
}

.sp-image-container:after {
  display: none;
}

/* Buttons
--------------------------------------------------*/
.sp-buttons {
  position: absolute;
  top: 50%;
  left: 100px;
  width: 100%;
  margin-top: -21px;;
}

.sp-button {
  display: block;
  width: 25px;
  height: 3px;
  margin-bottom: 10px;
  opacity: .5;
  border: 1px solid transparent;
  background-color: white;
  cursor: pointer;
  transition: all .3s;
}
.sp-button:hover,
.sp-selected-button {
  width: 50px;
  height: 6px;
  opacity: 1;
  border-color: rgba(255,255,255,.5);
  background-color: transparent;
}


/* Arrows
--------------------------------------------------*/
.sp-arrows {
  position: absolute;
}

.sp-fade-arrows {
  opacity: .5;
  transition: opacity .5s;
}

.sp-slides-container:hover .sp-fade-arrows {
  opacity: 1;
}

.sp-horizontal .sp-arrows {
  z-index: 10;
  top: 50%;
  right: 0;
  width: 100%;
  margin-top: 0;
  text-align: right;
}

.sp-vertical .sp-arrows {
  top: 0;
  left: 50%;
  height: 100%;
  margin-left: -10px;
}

.sp-arrow {
  position: absolute;
  bottom: 0;
  display: block;
  width: 35px;
  height: 35px;
  padding-top: 10px;
  opacity: .5;
  color: #fff;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  cursor: pointer;
  transition: all .3s;
  text-align: center;
  &:hover {
    opacity: 1;
  }
}

.sp-horizontal .sp-previous-arrow {
  left: 80px;
  transform: rotate(-45deg);
}

.sp-horizontal .sp-next-arrow {
  right: 80px;
  transform: rotate(135deg);
}

.sp-vertical .sp-previous-arrow {
  top: 20px;
}

.sp-vertical .sp-next-arrow {
  bottom: 20px;
}
