.section-title-page {
  position: relative;
  margin-top: 170px;
  padding-top: 95px;
  padding-bottom: 75px;
  background-image: url('../media/content/b-title-page/bg-1.jpg');
  background-size: cover;

  &__decor {
    position: absolute;
    width: 795px;
    top: 0;
    bottom: 0;
    left: calc(50% - 400px);
    background-color: #131313;
    opacity: 0.6;
    clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
  }
  &_mod-a {
    display: table;
    width: 100%;
    height: 900px;
    margin-top: 0;
    background-image: url('../media/content/b-title-page/bg-3.jpg');
    &:before {
      position: absolute;
      z-index: 10;
      right: 0;
      bottom: 0;
      left: 0;
      height: 400px;
      opacity: .8;
      background-image: linear-gradient(to top, #000 0%, rgba(0, 0, 0, 0) 100%);
      content: '';
    }
    .area-bg__inner {
      display: table-cell;
      padding-top: 30px;
      padding-bottom: 30px;
      vertical-align: middle;
    }
    .b-title-page {
      margin-top: 20px;
      margin-bottom: 25px;
      font-size: 72px;
      letter-spacing: -1.8px;
      text-transform: none;
    }
  }
  &_mod-b {
    height: 470px;
    margin-top: 0;
    padding-top: 245px;
    background-image: url('../media/content/b-title-page/bg-2.jpg');
  }
}

.b-title-page {
  margin-top: 0;
  margin-bottom: 0;
  font: 700 60px/1 Montserrat;
  color: #fff;
  letter-spacing: -1.5px;
  text-transform: uppercase;

  &__subtitle {
    margin-bottom: 30px;
    font-family: Montserrat;
    font-size: 16px;
    color: #fff;
    letter-spacing: 3.2px;
    text-transform: uppercase;
  }
  &__label {
    position: relative;
    display: inline-block;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: -.6px;
    &:after {
      position: absolute;
      right: 0;
      bottom: -3px;
      left: 0;
      display: block;
      height: 1px;
      content: '';
      @supports (clip-path: polygon(0 0)) {
        height: 6px;

        clip-path: polygon(0 100%, 100% 0, 100% 100%);
      }
    }
  }
}
