.b-main-filter {
  position: relative;
  z-index: 1;
  padding: 35px 60px 0;
  background: url('../media/content/b-main-filter/bg-1.jpg') 50%;
  background-size: cover;

  &__wrap {
    position: relative;
    bottom: -85px;
  }


  &__logo {
    margin-bottom: 12px;
  }

  &__decor {
    display: inline-block;
    width: 40px;
    height: 1px;
    margin-bottom: 20px;
    opacity: .5;
    background-color: #fff;
    box-shadow: 25px 5px 0 0 #fff,  -25px 5px 0 0 #fff;
  }

  &__cat {
    margin-top: 25px;
    margin-bottom: 65px;
  }
  &__cat-item {
    margin-right: 5px;
    margin-left: 5px;
    padding: 0 20px 10px;
    font: 11px/1 Montserrat;
    color: rgba(255, 255, 255, .4);
    border: 2px solid transparent;
    border-radius: 2px;
    transition: all .3s;
    text-align: center;
    text-transform: uppercase;
    .ic {
      display: block;
      margin-right: 0;
      font-size: 70px;
      line-height: .8;
      opacity: .4;
      color: #ccc;
      transition: all .3s;
    }
    &.active,
    &:hover {
      .ic {
        opacity: 1;
        color: #fff;
      }
    }
  }
  & &__item {
    padding-left: 3px;
  }
  &__label {
    margin-bottom: 20px;
    padding-left: 25px;
    font: 700 14px Montserrat;
    color: #253241;
    text-align: left;
  }
  &__selector {
    .ui-select {
      height: 59px;
      padding-top: 19px;
      padding-bottom: 19px;
    }
  }

  &__checkbox-item {
    display: inline-block;
    margin-right: 20px;
  }
  .form-group {
    margin-bottom: 15px;
  }
  .ui-title {
    position: relative;
  }
  .ui-title-logo {
    margin-top: -55px;
    margin-bottom: 0;
  }
  .ui-select {
    background-color: #f2f6f7;
  }
}

.b-main-filter-nav {
  margin-bottom: 0;

  justify-content: center;
  &__item {
    font-family: Montserrat;
    background-color: #293c4e;
  }
  &__link {
    position: relative;
    &:after {
      position: absolute;
      top: 5px;
      left: 5px;
      border-width: 7px 0 0 7px;
      border-style: solid;
      border-color: transparent;
      content: '';
    }
  }
  & &__link {
    margin-right: 0;
    padding: 17px 50px;
    font-size: 22px;
    color: rgba(255, 255, 255, .8);
    border: none;
    &:after {
      position: absolute;
      top: 5px;
      left: 5px;
      border: solid 7px transparent;
      content: '';
    }
    &.active,
    &:hover {
      color: #253241;
      background-color: #fff;
    }
    .ic {
      margin-right: 12px;
      font-size: 16px;
    }
  }
}

.b-main-filter-content {
  padding: 55px 50px 70px;
  background-color: #fff;
  box-shadow: 0 5px 35px 5px rgba(0, 0, 0, .12);
}
