.b-team {
  margin-top: 30px;
  margin-bottom: 35px;
  padding-bottom: 80px;
  &__media {
    width: 100%;
    height: 250px;
    img {
      height: 100%;
    }
  }
  &__inner {
    margin-right: 15px;
    margin-left: 15px;
    padding: 45px 45px 35px;
    background-color: #f2f6f7;
    transition: all .3s;
  }
  &__name {
    margin-top: 44px;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 700;
    color: #222;
  }
  &__category {
    margin-bottom: 30px;
    font-family: Montserrat;
    font-size: 14px;
    line-height: 1;
    color: #666;
  }

  &:hover & {
    &__inner {
      border-color: transparent;
      background-color: #fff;
      box-shadow: 0 5px 30px 5px rgba(0, 0, 0, .12);
    }
  }
}

.b-team-soc {
  margin-top: 20px;
  &__item {
    display: inline-block;
    margin: 0 14px;
  }
  &__link {
    font-size: 16px;
    color: #ccc;
  }
}
