.section-bnr {
  position: relative;
  z-index: 1;
  color: #fff;
  background-image: url('../media/content/b-bnr/bg-2.png');
  background-position: 50%;
  background-size: cover;
}

.b-bnr {
  font-size: 18px;
  line-height: 1.67;
  color: #fff;

  &__title {
    margin-bottom: 0;
    font: 700 36px Montserrat;
    color: #fff;
  }
}

.b-bnr-contacts {
  position: relative;
  padding: 7px 15px 0;
  text-align: center;


  &__item {
    font-size: 24px;
    .ic {
      margin-right: 10px;
    }
  }
  &__link {
    display: inline-block;
    font-size: 30px;
    font-weight: 700;
    color: #fff;
    &:hover {
      text-decoration: none;
    }
  }
}



.section-bnr-2 {
  position: relative;
  .container {
    position: relative;
  }
}

.b-bnr-2 {
  position: relative;
  font-size: 16px;
  color: #fff;
  &__inner {
    position: relative;
    z-index: 1;
    display: inline-block;
    width: 520px;
    padding: 107px 45px 65px;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: contain;
  }
  &__title {
    margin-top: 30px;
    font-size: 30px;
    line-height: 1.2;
    color: #fff;
    text-transform: uppercase;
  }
  &__info {
    display: inline-block;
    max-width: 250px;
    margin-bottom: 30px;
  }
  &__figure {
    position: absolute;
    z-index: 2;
    bottom: 0;
    max-width: 36vw;
  }
  .btn {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &:before {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 560px;
    background-color: #f2f6f7;
    content: '';
  }

  &_first {
    text-align: right;
    &:before {
      left: 0;

      clip-path: polygon(77% 0, 100% 38%, 74% 100%, 0 100%, 0 0);
    }
    .b-bnr-2 {
      &__figure {
        left: 0;
      }
      &__inner {
        background-image: url('../media/content/b-bnr/bg-1.png');

        clip-path: polygon(100% 0, 100% 100%, 24% 100%, 0 65%, 26% 0);
      }
    }
  }
  &_last {
    &:before {
      right: 0;

      clip-path: polygon(100% 0, 100% 100%, 26% 100%, 0 38%, 23% 0);
    }
    .b-bnr-2 {
      &__figure {
        right: 0;
      }
      &__inner {
        background-image: url('../media/content/b-bnr/bg-1_mirror.png');

        clip-path: polygon(74% 0, 100% 65%, 76% 100%, 0 100%, 0 0);
      }
    }
  }
}
