/* ======= GLOBAL ======= */
body {
  font: 15px/1.6 'Lato';
  color: #555;
}

.l-theme {
  position: relative;
  overflow-x: hidden;
  max-width: 1920px;
  margin: auto;
  background-color: #fff;
}

.l-main-content {
  position: relative;
  padding-top: 75px;
  padding-bottom: 100px;
  border-top: 1px solid #ddd;
}

.l-main-content_pt_0 {
  padding-top: 0;
}

.l-sidebar_top_minus {
  margin-top: -77px;
}

a {
  transition: all .3s;
}

/* ======= GUI ======= */
/* Titles */
.ui-title-page {
  position: relative;
  display: inline-block;
  margin-top: 0;
}

.ui-title {
  margin-bottom: 15px;
  font: 700 40px/1.3 Montserrat;
  color: #222;
}

.ui-title_lg {
  font-size: 48px;
  font-weight: 400;
}

.ui-title_light {
  color: #fff;
}

.ui-title-logo {
  margin-bottom: -40px;
  font: 700 90px/1 Montserrat;
  color: #f2f6f7;
  text-transform: uppercase;
}

.ui-title-logo_dark {
  color: #292929;
}

.ui-title-inner {
  font-size: 22px;
}

.ui-subtitle {
  margin-bottom: 4px;
  font-size: 14px;
  color: #222;
  text-transform: uppercase;
}

.ui-decor {
  position: absolute;
  top: -1px;
  right: 0;
  left: 9%;
  display: block;
  height: 1px;
  background: #fff;
}

@supports (clip-path: polygon(0 0)) {
  .ui-decor {
    height: 52px;
    clip-path: polygon(0 0, 100% 0, 96% 100%);
  }
}

@supports (clip-path: polygon(0 0)) {
  .ui-decor_mirror {
    clip-path: polygon(0 0, 100% 0, 4% 100%);
  }
}

.ui-decor_down {
  top: auto;
  bottom: -1px;
}

@supports (clip-path: polygon(0 0)) {
  .ui-decor_down {
    clip-path: polygon(4% 0, 100% 100%, 0% 100%);
  }
}

.ui-decor_center {
  right: 9%;
}

.ui-decor_full_width {
  left: 0;
}

.ui-decor_up {
  z-index: 10;
}

@supports (clip-path: polygon(0 0)) {
  .ui-decor_sm-h {
    height: 32px;
    clip-path: polygon(0 0, 100% 0, 2.5% 100%);
  }
}

.ui-rating {
  display: flex;
}

.ui-rating li {
  font-size: 10px;
  color: #ccc;
}

.ui-rating li.active {
  color: #ffce4a;
}

.ui-slider-nav {
  margin-top: 20px;
}

.ui-slider-nav .slick-slide {
  position: relative;
  margin-right: 20px;
}

.ui-slider-nav .slick-slide:after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  border: 4px solid #fff;
  background-color: #000;
  content: '';
  transition: all .3s;
}

.ui-slider-nav .slick-slide.slick-current:after, .ui-slider-nav .slick-slide:hover:after {
  opacity: .75;
}

.ui-slider_dots-gray .slick-dots li button {
  margin-right: 10px;
  margin-left: 10px;
  opacity: 1;
  border-color: #eee;
  background-color: #eee;
}

.ui-slider_arr_btm-center .slick-arrow {
  top: auto;
  bottom: 0;
}

.ui-slider_arr_btm-center .slick-dots {
  bottom: 33px;
}

.ui-slider_arr_btm-center .slick-prev {
  left: calc(50% - 150px);
}

.ui-slider_arr_btm-center .slick-next {
  right: calc(50% - 120px);
}

.ui-slider_arr_gray .slick-arrow:after {
  background-color: #ccc;
}

.ui-slider_arr_gray .slick-arrow:before {
  border-color: #ccc;
}

/* Images */
.img-scale {
  width: 100%;
  font-family: 'object-fit: cover;';
  object-fit: cover;
}

.section-gallery {
  padding-top: 115px;
  padding-bottom: 77px;
}

.ui-gallery {
  overflow: hidden;
  margin-top: 65px;
  margin-right: -7px;
  margin-left: -7px;
}

.ui-gallery__img {
  position: relative;
  display: block;
  height: 320px;
  margin-right: 7px;
  margin-bottom: 15px;
  margin-left: 7px;
}

.ui-gallery__img:before {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 75px;
  opacity: 0;
  content: '';
  transition: all .3s;
  clip-path: polygon(0 65%, 100% 0, 100% 100%, 0% 100%);
}

.ui-gallery__img:after {
  position: absolute;
  right: 30px;
  bottom: 24px;
  font: 900 24px/1 'Font Awesome 5 Free';
  opacity: 0;
  color: #fff;
  content: '\f00e';
  transition: all .3s;
}

.ui-gallery__img img {
  height: 100%;
}

.ui-gallery__img:hover:before {
  opacity: 1;
}

.ui-gallery__img:hover:after {
  opacity: 1;
}

/* Button */
.btn-default {
  color: #222;
  border: 2px solid #222;
  background-color: transparent;
}

.btn-default:hover {
  color: #e1eaf4;
}

.btn-primary {
  color: #fff;
}

.btn-secondary {
  color: #fff;
  background-color: #222;
}

.btn-secondary:hover {
  color: #e1eaf4;
}

.btn-white {
  color: #fff;
  border: 1px solid #fff;
}

.btn-white:hover {
  color: #555;
  background-color: #fff;
}

.btn-arr-wrap {
  font-family: Montserrat;
  font-size: 11px;
  font-weight: 600;
  color: #222;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: all .3s;
  text-align: center;
  text-transform: uppercase;
}

.btn-arr {
  position: relative;
  display: inline-block;
  padding-right: 20px;
  padding-left: 20px;
  line-height: 1;
}

.btn-arr:before {
  display: block;
  width: 24px;
  height: 24px;
  color: white;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  content: '';
  transition: all .3s;
}

.btn-arr:after {
  position: absolute;
  top: calc(50% - 1px);
  display: inline-block;
  width: 33px;
  height: 2px;
  background-color: #fff;
  content: '';
  transition: all .3s;
}

.btn-arr_left:before {
  transform: rotate(-45deg);
}

.btn-arr_left:after {
  right: 0;
}

.btn-arr_right:before {
  transform: rotate(135deg);
}

.btn-arr_right:after {
  left: 0;
}

.btn-arr_down {
  position: relative;
  display: block;
  margin-bottom: 15px;
}

.btn-arr_down:before {
  display: inline-block;
  transform: rotate(-135deg);
}

.btn-arr_down:after {
  top: 0;
  right: calc(50% - 16px);
  transform: rotate(90deg);
}

.btn .ic {
  margin-right: 9px;
  font-size: 16px;
}

.btn .ic-r {
  margin-left: 13px;
  font-size: 18px;
}

.ui-link {
  display: inline-block;
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 500;
}

/* Lists */
p + .list {
  margin-top: 36px;
}

.list {
  margin-bottom: 40px;
  padding-left: 0;
  list-style-type: none;
}

.list li {
  position: relative;
  margin-bottom: 4px;
  padding-left: 21px;
}

.list li:before {
  position: absolute;
  top: 7px;
  left: 1px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}

.list li > a {
  color: #777;
}

.list li > a:hover {
  text-decoration: none;
}

.list-mark-1 li {
  padding-left: 39px;
}

.list-mark-1 li:before {
  left: 17px;
  width: 4px;
  height: 15px;
  content: '';
}

.list-mark-2 li:before {
  font-family: 'Font Awesome 5 Free';
  font-size: 12px;
  content: '\f14a';
}

.list-mark-3 li:before {
  font-family: 'Font Awesome 5 Free';
  font-size: 12px;
  font-weight: 900;
  content: '\f101';
}

.list-mark-4 li:before {
  font-family: 'ElegantIcons';
  content: '\24';
}

.list-num {
  counter-reset: list;
}

.list-num li:before {
  display: inline-block;
  font-weight: 700;
  color: #980aac;
  content: counter(list) ". ";
  counter-increment: list;
}

/* Forms */
select:focus {
  outline-style: none;
}

input::-webkit-input-placeholder {
  color: #666;
}

input::-moz-placeholder {
  color: #666;
}

input:-ms-input-placeholder {
  color: #666;
}

.ui-form .form-label {
  margin-bottom: 20px;
  font-weight: 700;
}

.ui-select {
  height: 50px;
  padding-top: 12px;
  padding-right: 20px;
  padding-left: 20px;
  font-size: 15px;
  color: #222;
  border: 1px solid #f4f4f4;
  border-radius: 2px;
  background-color: #fff;
  text-transform: none;
}

.ui-select:focus {
  outline: none !important;
}

.ui-select:after {
  width: auto;
  height: auto;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  border: none;
  content: '\f107';
  vertical-align: unset;
}

.ui-select_sm {
  height: 45px;
  padding-top: 10px;
}

/* Tables */
.table th:first-child,
.table td:first-child {
  padding-left: 20px;
}

/* Blockquote */
blockquote cite {
  display: block;
  font-style: normal;
}

/* Alert */
.alert {
  letter-spacing: .05em;
}

.alert .icon {
  display: inline-block;
  margin-right: 18px;
  font-size: 16px;
  vertical-align: middle;
}

.alert__inner {
  padding-left: 70px;
}

.alert .close {
  position: absolute;
  top: 50%;
  right: 25px;
  margin-top: -9px;
  font-size: 18px;
  opacity: .3;
  color: #222;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.alert .close:hover {
  opacity: 1;
}

.alert-default {
  color: #666;
  background-color: #f4f4f4;
}

.alert-default .alert-icon .icon {
  color: #444;
}

.alert-1 {
  background-color: #e1e279;
}

.alert-2 {
  color: #fff;
}

.alert-3 {
  background-color: #ffc;
}

.alert-3 .alert-icon .icon {
  color: #bbbc5a;
}

.alert-4 {
  background-color: #ffe6e6;
}

.alert-4 .alert-icon .icon {
  color: #d8504f;
}

.alert-5 {
  background-color: #d0ffd1;
}

.alert-5 .alert-icon .icon {
  color: #45bd47;
}

.alert-block {
  margin-bottom: 21px;
  padding: 22px 50px 30px;
  text-shadow: none;
}

.alert-title {
  margin-bottom: 9px;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0;
  text-transform: uppercase;
}

.alert-text {
  color: #333;
  letter-spacing: .025em;
}

.alert-icon {
  float: left;
  margin-top: 20px;
}

.alert-icon .icon {
  font-size: 30px;
}

/* Sections */
.section-area {
  position: relative;
}

.section-default {
  position: relative;
  padding-top: 103px;
  padding-bottom: 103px;
}

.section-inner {
  margin-top: 60px;
}

.area-bg {
  position: relative;
}

.area-bg:after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: .8;
  content: '';
}

.area-bg__inner {
  position: relative;
  z-index: 110;
}

.area-bg_dark:after {
  background-color: #1f1f1f;
}

.area-bg_light:after {
  background-color: #f1f1f1;
}

.area-bg_op_25:after {
  opacity: .25;
}

.area-bg_op_30:after {
  opacity: .3;
}

.area-bg_op_40:after {
  opacity: .4;
}

.area-bg_op_50:after {
  opacity: .5;
}

.area-bg_op_60:after {
  opacity: .6;
}

.area-bg_op_70:after {
  opacity: .7;
}

.area-bg_op_75:after {
  opacity: .75;
}

.area-bg_op_80:after {
  opacity: .8;
}

.area-bg_op_85:after {
  opacity: .85;
}

.area-bg_op_90:after {
  opacity: .9;
}

.area-bg_op_100:after {
  opacity: 1;
}

/* Accordion */
.ui-accordion__link {
  position: relative;
  width: 100%;
  padding: 22px 65px;
  font-size: 15px;
  font-weight: 700;
  color: #222;
  border: none;
  background-color: transparent;
  background-color: #f4f4f4;
  text-align: left;
}

.ui-accordion__link .ic {
  position: absolute;
  top: calc(50% - 7px);
  right: 25px;
  font-size: 14px;
  color: #555;
  transition: all .3s;
}

.ui-accordion__link[aria-expanded='true'] .ic, .ui-accordion__link:hover .ic {
  transform: rotate(180deg);
}

.ui-accordion__link[aria-expanded='true'] .ui-accordion__number, .ui-accordion__link:hover .ui-accordion__number {
  color: #fff;
}

.ui-accordion__number {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 100%;
  padding-left: 12px;
  font-size: 18px;
  line-height: 58px;
  color: #aaa;
  background-color: #ededed;
  transition: all .3s;
  clip-path: polygon(0 0, 80% 0%, 100% 100%, 0% 100%);
}

.ui-accordion .card {
  margin-bottom: 15px;
  border-bottom: none;
}

.ui-accordion .card-header {
  padding: 0;
}

.ui-accordion .card-body {
  margin-top: 25px;
  margin-bottom: 10px;
  padding-bottom: 0;
  padding-left: 36px;
  border-left: 3px solid #222;
}

/* Other */
.color-dark {
  font-weight: 700;
}

.color-white {
  color: #fff;
}

.bg-white {
  background-color: #fff;
}

.bg-gray {
  background-color: #f2f6f7;
}

.bg-dark {
  color: #999;
