.b-booking-group {
  padding-top: 82px;
  padding-bottom: 75px;
}

.b-booking-group .form-label {
  margin-bottom: 18px;
}

.b-booking-group .form-group {
  margin-bottom: 36px;
}

.b-booking-nav {
  padding-right: 80px;
}

.b-booking-nav__item {
  position: relative;
  display: block;
  margin-bottom: 15px;
  padding-bottom: 10px;
  background-color: #f2f6f7;
}

.b-booking-nav__item:hover {
  text-decoration: none;
}

.b-booking-nav__item .ic {
  position: absolute;
  top: 34px;
  left: 28px;
  font-size: 18px;
  color: #999;
}

.b-booking-nav__item.active .ic {
  color: #fff;
}

.b-booking-nav__item.active .b-booking-nav__info {
  color: #fff;
}

.b-booking-nav__item.active .b-booking-nav__main {
  margin-right: -10px;
  border-right-color: transparent;
}

.b-booking-nav__item.active .b-booking-nav__main:before {
  opacity: 1;
}

.b-booking-nav__item.active .b-booking-nav__main:after {
  border-color: #fff transparent transparent #fff;
}

.b-booking-nav__header {
  padding: 26px 27px 14px;
  font-size: 18px;
  color: #94b9c1;
  text-transform: uppercase;
}

.b-booking-nav__main {
  position: relative;
  padding: 28px 20px 35px 60px;
  border-right: 3px solid;
  background-color: #444;
}

.b-booking-nav__main:after {
  position: absolute;
  top: 4px;
  left: 4px;
  border-width: 6px;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.15) transparent transparent rgba(255, 255, 255, 0.15);
  content: '';
}

.b-booking-nav__main:before {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 100%;
  margin-left: 3px;
  opacity: 0;
  border-top: 53px solid transparent;
  border-bottom: 53px solid transparent;
  border-left: 30px solid;
  content: '';
}

.b-booking-nav__title {
  display: block;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
}

.b-booking-nav__info {
  display: block;
  line-height: 1;
  color: #999;
}

.b-booking-main {
  padding-left: 30px;
}

.b-booking-main__section {
  margin-bottom: 65px;
