/*GRID FLEEP*/
.flip-active .flipper {
  transform: rotateY(180deg);
}

.flip-active .flip__front,
.flip-active .flip__back {
  backface-visibility: visible;
}

.flip-active .flip__front {
  opacity: 0;
}

.flip-active .flip__back {
  z-index: 10;
  box-shadow: 0 6px 23px 2px rgba(0, 0, 0, 0.15);
}

.flip-active .flip-btn {
  opacity: 0;
}

@supports (transform-style: preserve-3d) {
  .flip-active .flip__front,
  .flip-active .flip__back {
    backface-visibility: hidden;
  }
  .flip-active .flip__front {
    opacity: 1;
  }
}

.flipper {
  position: relative;
  transition: .6s;
  transform-style: preserve-3d;
}

.flip-container {
  width: 100%;
}

.flip__front, .flip__back {
  width: 100%;
}

.flip__back {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #fff;
}

.flip__front {
  z-index: 2;
  background-color: #fff;
  transform: rotateY(0deg);
}

.flip__footer {
  padding: 35px 30px;
}

.flip__back {
  transform: rotateY(180deg);
  text-align: left;
}

.flip__back .b-goods-flip__header {
  margin-bottom: 0;
  padding: 40px 35px 40px 30px;
  background: #222;
}

.flip__back .b-goods-flip__title {
  margin-bottom: 0;
  padding-bottom: 4px;
  font: 700 22px/1 Montserrat;
  color: #fff;
  border-bottom: none;
}

.flip__back .b-goods-flip__category {
  font-size: 16px;
  color: #ccc;
}

.flip-container {
  perspective: 1000px;
}

.flip-btn {
  position: absolute;
  z-index: 99;
  top: 25px;
  right: 25px;
  display: block;
  width: 45px;
  height: 45px;
