.b-semicircle {
  position: relative;
  overflow: hidden;
  width: 260px;
  height: 130px;
  text-align: left;
}

.b-semicircle:before, .b-semicircle:after {
  position: absolute;
}

.b-semicircle:before {
  width: inherit;
  height: inherit;
  border: 45px solid rgba(211, 211, 211, 0.3);
  border-bottom: none;
  border-top-left-radius: 175px;
  border-top-right-radius: 175px;
  content: '';
}

.b-semicircle__inner {
  position: absolute;
  z-index: 4;
  top: 100%;
  left: 0;
  width: inherit;
  height: inherit;
  border: 45px solid;
  border-top: none;
  border-bottom-right-radius: 175px;
  border-bottom-left-radius: 175px;
  transform-origin: 50% 0;
  animation-duration: .4s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}

.b-semicircle__number {
  margin-top: -40px;
  font-family: Montserrat;
  font-size: 36px;
  font-weight: 600;
}

.b-semicircle__title {
  margin-top: 10px;
  font-family: Montserrat;
  font-size: 18px;
  color: #fff;
}

.b-semicircle_mod-a .b-semicircle__inner {
  animation-name: rotate-one;
}

.b-semicircle_mod-b .b-semicircle__inner {
  animation-name: rotate-two;
}

.b-semicircle_mod-c .b-semicircle__inner {
  animation-name: rotate-three;
}

.b-semicircle_mod-d .b-semicircle__inner {
  animation-name: rotate-four;
}

/* ANIMATIONS
 */
@keyframes rotate-one {
  100% {
    transform: rotate(153deg);
    /**
     * 180deg * 85% = 153deg
     */
  }
}

@keyframes rotate-two {
  100% {
    transform: rotate(90deg);
  }
}

@keyframes rotate-three {
  100% {
    transform: rotate(162deg);
  }
}

@keyframes rotate-four {
  100% {
    transform: rotate(39.6deg);
  }
}

