/*GRID FLEEP*/

.flip-active {
  .flipper {
    transform: rotateY(180deg);
  }
  .flip__front,
  .flip__back {
    backface-visibility: visible;
  }
  .flip__front {
    opacity: 0;
  }
  .flip__back {
    z-index: 10;
    box-shadow: 0 6px 23px 2px rgba(0, 0, 0, .15);
  }
  .flip-btn {
    opacity: 0;
  }
}

@supports (transform-style: preserve-3d) {
  .flip-active {
    .flip__front,
    .flip__back {
      backface-visibility: hidden;
    }
    .flip__front {
      opacity: 1;
    }
  }
}

.flipper {
  position: relative;
  transition: .6s;

  transform-style: preserve-3d;
}
.flip-container {
  width: 100%;
}

.flip {
  &__front,
  &__back {
    width: 100%;
  }

  &__back {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background-color: #fff;
  }


  &__front {
    z-index: 2;
    background-color: #fff;
    transform: rotateY(0deg);
  }
  &__footer {
    padding: 35px 30px;
  }
}


.flip__back {
  transform: rotateY(180deg);
  text-align: left;

  .b-goods-flip__header {
    margin-bottom: 0;
    padding: 40px 35px 40px 30px;
    background: #222;
  }

  .b-goods-flip__title {
    margin-bottom: 0;
    padding-bottom: 4px;
    font: 700 22px/1 Montserrat;
    color: #fff;
    border-bottom: none;
  }
  .b-goods-flip__category {
    font-size: 16px;
    color: #ccc;
  }
}

.flip-container {
  perspective: 1000px;
}


.flip-btn {
  position: absolute;
  z-index: 99;
  top: 25px;
  right: 25px;
  display: block;
  width: 45px;
  height: 45px;
  margin: 0;
  padding: 16px 15px;
  border: 0;
  border-radius: 50%;
  outline: none !important;
  background: #fff;
  box-shadow: 0 5px 35px 5px rgba(0, 0, 0, .12);
  cursor: pointer;
  transition: all .3s;
  text-align: center;

  &.toggle-menu-button .toggle-menu-button-icon {
    width: 16px;
    height: 16px;
  }
  span {
    display: block;
    width: 100%;
    height: 0;
    margin-bottom: 3px;
    font-size: 11px;
    line-height: 0;
    border: 1px solid #fff;
    vertical-align: top;
  }
}

.flip-btn-hide {
  position: absolute;
  top: 34px;
  right: 34px;
  width: 23px;
  height: 23px;
  background: url('data:image/png;base64,') 50%;
