.b-offers-slider {
  margin-top: 45px;
  margin-bottom: -295px;
  .slick-current + .slick-slide {
    .b-offers {
      &__img {
        opacity: 0;
      }
      &__wrap {
        box-shadow: 0 5px 30px 5px rgba(0, 0, 0, .2);
      }
    }
  }
  .slick-arrow {
    top: 175px;
  }
  .slick-prev {
    left: -130px;
  }
  .slick-next {
    right: -110px;
  }
}

.section-offers {
  padding-bottom: 240px;
  &__inner {
    padding-top: 110px;
    border-bottom: 1px solid transparent;
  }
}

.b-offers {
  padding-bottom: 35px;
  text-align: center;
  &__wrap {
    margin-right: 15px;
    margin-left: 15px;
    background-color: #f2f6f7;
    transition: all .3s;
  }
  &__ic {
    display: inline-block;
    padding-top: 50px;
    padding-bottom: 20px;
    font-size: 80px;
    line-height: 1;
  }
  &__title {
    margin-bottom: 35px;
    font-family: Montserrat;
    font-size: 20px;
    font-weight: 700;
    color: #222;
    text-transform: uppercase;
  }
  &__inner {
    position: relative;
    display: table;
    width: 100%;
    height: 300px;
    padding-top: 40px;

    clip-path: polygon(0% 5%, 0 100%, 100% 100%, 100% 0, 20% 9%);
  }
  &__img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 260px;
    transition: all .3s;
    img {
      height: 100%;
    }
  }
  &__info {
    display: table-cell;
    padding-right: 50px;
    padding-left: 50px;
    color: #fff;
    vertical-align: middle;
  }

  &:hover & {
    &__img {
      opacity: 0;
    }
  }
}
