.b-stages {
  margin-top: 45px;
  margin-bottom: 72px;
  padding: 0 9%;
  &__item {
    position: relative;
    text-align: center;
    &:not(:last-child):after {
      position: absolute;
      top: 83px;
      right: -55px;
      width: 130px;
      height: 130px;
      background: url('data:image/png;base64,') no-repeat;
      content: '';
    }
    &:nth-child(even) {
      padding-top: 60px;
      &:after {
        background: url('data:image/png;base64,');
      }
    }
    &:nth-child(even) {
      .b-stages__number {
        margin-top: 25px;
      }
    }
  }
  &__number {
    position: relative;
    display: block;
    margin-bottom: 20px;
    font: 700 120px/1 Montserrat;
    color: #f2f6f7;
    &:after {
      position: absolute;
      top: calc(50% - 10px);
      left: calc(50% - 10px);
      width: 20px;
      height: 20px;
      border: 5px solid #fff;
      border-radius: 50%;
      box-shadow: 0 5px 35px 5px rgba(0, 0, 0, .12);
      content: '';
    }
  }
  &__title {
    margin-bottom: 15px;
    font: 700 18px Montserrat;
  }
  &__info {
    overflow: hidden;
    height: 72px;
    padding-right: 50px;
    padding-left: 50px;
  }
}
