.section-special {
  color: #fff;
  background: url('../media/content/b-special/bg.png') no-repeat 81.4% 100%;
  &__img {
    margin-left: -40px;
  }
  &__inner {
    position: relative;
    z-index: 1;
    margin-top: 105px;
    margin-right: -214px;
    margin-bottom: 30px;
    margin-left: 105px;
    padding: 90px 80px 90px;
  }
  &__info {
    font-size: 18px;
  }
  .ui-title-logo {
    opacity: .05;
  }
}

.b-special-slider {
  margin-top: 50px;
}

.b-special {
  margin-bottom: 50px;
  padding-right: 20px;
  padding-left: 20px;
  &__img {
    height: 200px;
    margin-bottom: 20px;
    img {
      height: 100%;
    }
  }
  &__title {
    display: block;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    text-align: center;
  }
}
