.b-seller {
  margin-bottom: 35px;
  &__header {
    padding: 20px 30px;
    color: #fff;
  }
  &__img {
    display: inline-block;
    overflow: hidden;
    width: 80px;
    height: 80px;
    margin-right: 8px;
    border-radius: 50%;
    vertical-align: middle;
    img {
      height: 100%;
    }
  }
  &__ic {
    position: absolute;
    top: 25px;
    left: 30px;
    font-size: 34px;
  }
  &__title {
    display: inline-block;
    line-height: 1;
    vertical-align: middle;
  }
  &__name {
    margin-bottom: 6px;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: -.24px;
  }
  &__category {
    font-size: 14px;
  }
  &__main {
    position: relative;
    margin-top: -1px;
    padding: 26px 20px 20px 75px;
    border: 1px solid #eee;
  }
  &__contact {
    position: relative;
    margin-bottom: 25px;
    font-size: 16px;
    color: #222;
    .ic {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__phone {
    display: inline-block;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    color: #222;
  }
}


.b-seller-soc {
  &__item {
    display: inline-block;
    padding-right: 12px;
    &:not(:first-child) {
      padding-left: 12px;
      border-left: 1px solid #ccc;
    }
  }
  &__link {
    font-size: 16px;
    color: #ccc;
  }
}
