.b-filter-goods {
  padding-bottom: 30px;

  &__wrap {
    display: inline-block;
  }

  &__info {
    color: #222;
  }
  &__select {
    display: inline-block;
    margin-right: 12px;
    vertical-align: middle;
  }
  &__btn {
    padding: 12px 10px 11px 22px;
    font-size: 13px;
    font-weight: 400;
    color: #222;
    border: 1px solid #ddd;
    background-color: #fff;
    text-transform: none;
    .badge {
      top: 0;
      width: 25px;
      height: 25px;
      margin-left: 15px;
      padding: 0;
      font-size: 15px;
      font-weight: 700;
      line-height: 25px;
      border-radius: 0;
      text-align: center;
    }
  }
  .ui-select {
    border-color: #ddd;
  }

  &_brd {
    margin-bottom: 50px;
    border-bottom: 1px solid #ddd;
  }

  &_center {
    text-align: center;
    .btns-switch {
      display: inline-block;
      margin-left: 2px;
      vertical-align: middle;
    }
  }
  &_center & {
    &__info {
      display: inline-block;
      margin-right: 70px;
    }
  }
}

.btns-switch {
  display: inline-block;
  text-align: right;
  vertical-align: middle;
  &__item {
    display: inline-block;
    padding-left: 20px;
    font-size: 16px;
    line-height: 43px;
    color: #ccc;
    cursor: pointer;
    transition: all .3s;
    text-align: center;
    vertical-align: middle;
    &:last-child {
      margin-left: 20px;
      border-left: 1px solid #ddd;
    }

    &.active {
      cursor: default;
    }
  }
}
