.section-progress {
  padding-top: 115px;
  padding-bottom: 65px;
  background: url('../media/content/b-progress/bg.jpg');
  background-size: cover;
  &__inner {
    text-align: center;
  }
  &__img {
    display: inline-block;
    max-width: 460px;
    vertical-align: top;
  }
}

.b-progress-list {
  display: inline-block;
  width: 535px;
  padding-right: 65px;
  padding-left: 65px;
  color: #fff;
  text-align: left;
  &__item {
    margin-bottom: 60px;
    padding-right: 25px;
    padding-left: 25px;
  }

  &__percent {
    display: block;
    margin-bottom: 4px;
    font: 48px/1 Montserrat;
  }
  &__name {
    display: block;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
  }
}
