/* ======= FOOTER ======= */

.footer {
  position: relative;
  z-index: 1;
  padding-top: 155px;
  padding-bottom: 40px;
  color: #999;
  background: url(../media/content/footer/bg.jpg) 50% 0;
  background-size: cover;
  &__decor {
    position: absolute;
    top: 0;
    right: 84px;
    left: 84px;
    display: block;
    height: 1px;
    background-color: #fff;
  }
  &__figure {
    position: absolute;
    z-index: 1;
    top: -180px;
    left: 0;
  }
  &__logo {
    display: block;
    max-width: 200px;
    margin-top: -10px;
    margin-bottom: 30px;
  }
}

@supports (clip-path: polygon(0 0)) {
  .footer__decor {
    height: 47px;

    clip-path: polygon(0 0, 100% 0, 94% 100%, 0 0);
  }
}

.footer-soc {
  margin-top: 25px;
  margin-bottom: 35px;
  &__item {
    display: inline-block;
    &:not(:last-child) {
      .footer-soc__link {
        padding-right: 33px;
      }
    }
  }
  &__link {
    display: inline-block;
    font-size: 16px;
    color: #fff;
    transition: all .3s;
    .ic {
      display: inline-block;
      line-height: 36px;
    }
  }
}

.footer-section {
  padding-top: 10px;
  &__title {
    margin-bottom: 28px;
    font: 400 18px Montserrat;
    color: #fff;
    &_sm {
      margin-bottom: 20px;
      font-family: Lato;
      font-weight: 700;
    }
  }
  .btn {
    padding: 15px 35px;
    font-size: 14px;
  }
}

.footer-info {
  margin-bottom: 18px;
  padding-bottom: 32px;
  border-bottom: 1px solid rgba(221, 221, 221, .1);
}

.footer-contacts {
  padding-top: 5px;
  padding-bottom: 22px;
  border-bottom: 1px solid rgba(221, 221, 221, .1);
  &__item {
    margin-bottom: 6px;
    color: #a9aeb3;
    .ic {
      display: inline-block;
      width: 27px;
      font-size: 14px;
    }
    a {
      color: #a9aeb3;
    }
  }
  & &__phone {
    font-size: 18px;
    font-weight: 700;
    color: #fff;
  }
}

.footer-list {
  li {
    margin-bottom: 5px;
    a {
      position: relative;
      padding-left: 15px;
      color: #cfcfcf;
      &:before {
        position: absolute;
        top: 0;
        left: 0;
        font-family: 'Font Awesome 5 Free';
        font-size: 12px;
        font-weight: 900;
        color: #ccc;
        content: '\f105';
      }
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.footer-form {
  position: relative;
  &__input {
    height: 55px;
    padding-left: 20px;
    font-family: Montserrat;
    font-size: 12px;
    font-size: 12px;
    font-size: 12px;
    color: #a9aeb3;
    border-radius: 0;
    box-shadow: none;
    letter-spacing: -.12px;

    @include placeholder {
      color: #a9aeb3;
      text-transform: uppercase;
    }
  }
  .form-group {
    margin-bottom: 10px;
  }
  .ic {
    position: absolute;
    top: 18px;
    right: 19px;
    font-size: 18px;
    color: #222;
  }
}

.footer-hours {
  margin-bottom: 25px;
  &__title {
    margin-bottom: 3px;
    font-weight: 700;
    color: #fff;
  }
}

.footer-copyright {
  margin-top: 50px;
  padding-top: 30px;
  color: #999;
  border-top: 1px solid rgba(221, 221, 221, .1);
  text-align: center;
  &__link {
    color: #aaa;
    text-decoration: underline;
  }
}
