.section-reviews {
  background: url('../media/content/b-reviews/bg.jpg') no-repeat;
  background-size: cover;
  .ui-title-logo {
    margin-bottom: -20px;
    opacity: .06;
    text-transform: none;
  }
}

.b-reviews-slider {
  margin-bottom: 50px;
}

.b-reviews {
  padding-right: 15px;
  padding-left: 15px;
  color: #fff;
  text-align: center;
  &__text {
    font-size: 18px;
    line-height: 1.89;
  }
  &__footer {
    margin-top: 35px;
    &:before {
      display: inline-block;
      font: 700 85px/1 Montserrat;
      content: '';
    }
  }
  &__name {
    margin-top: -30px;
    font: 18px Montserrat;
    letter-spacing: .45px;
  }
  &__category {
    font-size: 12px;
    text-transform: uppercase;
  }
}
