.b-filter {
  &__row {
    margin-bottom: 15px;
    &.row {
      margin-right: -5px;
      margin-left: -5px;
    }
  }
  &__item {
    padding-right: 5px;
    padding-left: 5px;
  }
  &__reset {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.b-filter-slider {
  margin-top: 34px;
  margin-bottom: 30px;
  &__title {
    margin-bottom: 24px;
    font: 700 16px Montserrat;
    color: #222;
    text-transform: uppercase;
  }
  .b-filter__row {
    margin-top: 20px;
  }

  .ui-select {
    width: 100%;
    padding-top: 0;
    color: #555;
    &:after {
      content: none;
    }
  }
}
