.b-steps {
  margin-bottom: 70px;
  padding-top: 112px;
  padding-bottom: 98px;
  background-image: url('../media/content/b-steps/bg.jpg');
  background-size: cover;
  text-align: center;
  &__item {
    display: inline-block;
    width: 255px;
    &:not(:first-child) {
      .b-steps__btn {
        &:after {
          position: absolute;
          top: 40px;
          right: 100%;
          display: block;
          width: 178px;
          height: 5px;
          content: '';
        }
      }
    }
  }
  &__btn {
    position: relative;
    display: inline-block;
    width: 80px;
    height: 80px;
    margin-bottom: 14px;
    padding: 0;
    font: 700 36px Montserrat;
    line-height: 80px;
    color: #fff;
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 0 7px rgba(255, 255, 255, .15);
    cursor: pointer;
    text-align: center;
    &:disabled {
      color: #ccc;
      border-color: #fff;
      background-color: #eee;
      box-shadow: none;
      &:after {
        background-color: #eee;
      }
       + .b-steps__info {
        color: #fff;
      }
    }
  }
  &__info {
    display: block;
    margin-top: 12px;
    font-family: Montserrat;
    font-size: 18px;
  }
}
