.b-services {
  position: relative;
  padding-top: 195px;
  padding-bottom: 100px;
}

.b-services-content {
  &__section {
    padding-top: 45px;
    padding-right: 60px;
  }
  &__subtitle {
    margin-top: 40px;
    margin-bottom: 20px;
    font: 400 24px Montserrat;
  }
  &__main {
    position: relative;
    &:before {
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      bottom: -80px;
      left: -80px;
      border-width: 0 0 10px 10px;
      border-style: solid;
      border-color: #f2f6f7;
      content: '';
    }
  }
}

.b-services-nav {
  &__inner {
    position: relative;
    margin-left: 40px;
    background: url('../media/content/b-services/bg-1.jpg');
    background-size: cover;
    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: .85;
      background-color: #f2f6f7;
      content: '';
      transition: all .3s;
    }
  }
  &__link {
    display: block;
    margin-bottom: 24px;
    padding: 0;
    color: #555;
    text-align: center;
    .ic {
      position: relative;
      z-index: 2;
      font-size: 60px;
      line-height: 174px;
      color: #222;
      transition: all .3s;
    }
  }
  &__link {
    &.active,
    &:hover {
      color: #fff;
      .ic {
        color: #fff;
      }
      .b-services-nav__title {
        color: #222;
        background-color: #fff;
      }
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    padding: 40px 30px 22px;
    font: 700 16px/1 Montserrat;
    color: #fff;
    transition: all .3s;

    clip-path: polygon(0% 10%, 0 100%, 100% 100%, 100% 0, 28% 24%);
  }
}
