/*01  Blog */

.b-post-group {
  margin-top: 60px;
  &_2-col {
    margin-right: -30px;
    margin-left: -30px;
  }
}

.b-post {
  .entry-title {
    font-weight: 700;
  }
  .entry-title,
  .entry-title a {
    color: #222;
  }
  .entry-label {
    display: inline-block;
    margin-right: 5px;
    margin-left: 5px;
    padding: 8px 15px;
    font: 600 11px/1 Montserrat;
    color: #222;
    border: 1px solid #ddd;
    text-transform: uppercase;
  }
  .entry-media {
    .tilter__figure::before {
      box-shadow: none;
    }
  }
  .entry-meta {
    padding-top: 15px;
    padding-bottom: 15px;
    &__item {
      display: inline-block;
      font: 600 14px Montserrat;
      color: #222;
      white-space: nowrap;
    }
    .ic {
      display: inline-block;
      margin-right: 5px;
      font-size: 16px;
      vertical-align: middle;
    }
    &__link {
      display: inline-block;
    }
  }

  .entry-header {
    position: relative;
  }
  p:last-child {
    margin-bottom: 0;
  }
}

.b-post-1 {
  margin-bottom: 45px;
  padding-right: 15px;
  padding-bottom: 35px;
  padding-left: 15px;
  .entry-title {
    margin-bottom: 20px;
    padding-right: 10%;
    font-family: Montserrat;
    font-size: 30px;
  }
  .entry-media {
    height: 300px;
    img {
      height: 100%;
    }
  }
  .entry-meta {
    margin-bottom: 14px;
    padding-top: 35px;
  }
  .entry-main {
    margin-bottom: 30px;
  }
}

.b-post-2 {
  margin-bottom: 100px;
  .entry-title {
    margin-bottom: 0;
    font-family: Montserrat;
    font-size: 30px;
  }
  .entry-meta {
    margin-bottom: 24px;
    padding-top: 25px;
    &__item {
      font-weight: 600;
    }
  }
  .entry-media {
    padding-right: 67px;
  }
  .entry-main {
    padding-left: 25px;
  }
  .entry-footer {
    margin-top: 46px;
    .btn {
      padding: 12px 30px;
      font-family: Montserrat;
      font-size: 14px;
      background-color: #f2f6f7;
      &:hover {
        color: #fff;
      }
    }
  }
}

.section-article {
  background: url('../media/content/b-post/bg/bg-1.png') 0 50% no-repeat;
}

.b-post-3 {
  margin-top: 30px;
  margin-right: 16px;
  margin-bottom: 48px;
  &:not(:first-child) {
    padding-top: 37px;
    border-top: 1px solid #ddd;
  }
  .entry-title {
    font: 700 22px Montserrat;
    color: #222;
  }
  .entry-meta {
    padding-top: 0;
    padding-bottom: 20px;
  }
  .entry-header {
    margin-bottom: 12px;
  }
  .entry-media {
    height: 214px;
    margin-bottom: 30px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}

.b-post-full {
  margin-top: -75px;
  padding-top: 137px;
  padding-bottom: 30px;
  background: url('../media/content/b-post/bg/bg-2.png') 50% 100% no-repeat;
  &:before {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 343px;
    background: url('../media/content/b-post/bg/bg-3.jpg') 50%;
    background-size: cover;
    content: '';
  }
  .ui-decor {
    height: 32px;
  }
  .entry-title {
    margin-top: 24px;
    margin-bottom: 23px;
    padding-top: 65px;
    font-size: 30px;
    border-top: 1px solid #ddd;
  }
  .entry-title-inner {
    margin-bottom: 16px;
    padding-top: 15px;
    font-size: 30px;
  }
  .entry-meta {
    padding-top: 40px;
    &__item {
      font-size: 14px;
      .ic {
        margin-right: 8px;
      }
    }
  }
  .entry-media {
    position: relative;
    margin-bottom: 15px;
  }


  .entry-blockquote {
    position: relative;
    margin-top: 67px;
    margin-bottom: 70px;
    padding-top: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
    padding-left: 173px;
    font-size: 24px;
    line-height: 36px;
    color: #222;
    border-top: 1px solid  #f2f6f7;
    border-bottom: 1px solid #f2f6f7;
    &:before {
      position: absolute;
      top: 70px;
      left: 70px;
      font-family: Montserrat;
      font-size: 100px;
      content: '';
    }
  }
  .entry-blockquote-footer {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 700;
    color: #555;
  }
  .entry-footer {
    margin-top: 100px;
    margin-right: -30px;
    margin-left: -30px;
    text-align: center;
  }

  .entry-tag-title {
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 600;
    color: #222;
  }
  .entry-tag {
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 600;
    &:hover {
      text-decoration: none;
    }
  }
  ul {
    margin-bottom: 40px;
    padding-left: 0;
    list-style-type: none;
    li {
      position: relative;
      margin-bottom: 9px;
      padding-left: 20px;
      &:before {
        position: absolute;
        top: 4px;
        left: -10px;
        font-family: 'Font Awesome 5 Free';
        font-size: 12px;
        font-weight: 900;
        content: '\f054';
      }
    }
  }
  .b-post-soc {
    &__title {
      display: inline-block;
      font-family: Montserrat;
      font-size: 14px;
      font-weight: 600;
      color: #222;
      vertical-align: middle;
    }
    &__item {
      display: inline-block;
      margin-left: 7px;
      vertical-align: middle;
    }
    &__link {
      display: block;
      width: 32px;
      height: 32px;
      font-size: 14px;
      line-height: 32px;
      color: #ccc;
      border-radius: 16px;
      background-color: #f2f6f7;
      text-align: center;
      &:hover {
        color: #fff;
      }
    }
  }
}


/*02  Comments */

.section-comment {
  margin-bottom: 60px;
  padding-top: 60px;
}
.comments-list {
  padding-top: 35px;
  .children {
    .comment {
      padding-left: 100px;
    }
  }
  .comment {
    margin-bottom: 35px;
    padding-bottom: 15px;
    border-bottom: 1px solid #eee;
  }
   > li:last-child .comment {
    border-bottom: none;
  }
  .comment-face {
    float: left;
    overflow: hidden;
    width: 80px;
    height: 80px;
    margin-top: 10px;
    border-radius: 50%;
  }
  .comment-inner {
    padding-left: 100px;
  }
  .comment-header {
    margin-bottom: 8px;
    padding-top: 10px;
    line-height: 1;
  }
  .comment-author {
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 10px;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 600;
    font-style: normal;
    color: #222;
  }
  .comment-datetime {
    display: inline-block;
    font-size: 13px;
  }
  .comment-btn {
    display: inline-block;
    margin-right: 10px;
    padding: 6px 14px;
    font-family: Montserrat;
    font-size: 12px;
    line-height: 1;
    color: #fff;
    text-transform: uppercase;
    &:hover {
      text-decoration: none;
    }
  }
}

.b-post-nav {
  margin-bottom: 45px;
  &__item {
    padding-top: 27px;
    padding-bottom: 27px;
    background-color: #f4f4f4;
    &_left {
      .b-post-nav__label {
        clip-path: polygon(0 0, 91% 0%, 100% 100%, 0% 100%);
      }
    }
    &_right {
      .b-post-nav__label {
        clip-path: polygon(9% 0, 100% 0, 100% 100%, 0% 100%);
      }
    }
    &:hover {
      text-decoration: none;
      .b-post-nav__label {
        background-color: #222;
      }
    }
  }
  &__label {
    display: block;
    padding: 18px 25px;
    font-size: 14px;
    color: #fff;
    transition: all .3s;
    text-transform: uppercase;
  }
  &__title {
    display: block;
    padding-right: 30px;
    padding-left: 30px;
    font-size: 18px;
    line-height: 26px;
    color: #222;
  }
}

.section-reply-form {
  margin-bottom: 155px;
  .form-reply {
    margin-top: 34px;
  }
  textarea {
    height: 312px;
  }
}
