.b-styles {
  padding-bottom: 130px;
  &__main {
    padding-top: 60px;
    padding-left: 68px;
  }
  &__title {
    margin-bottom: 12px;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    color: #222;
  }

  &__group {
    margin-top: -80px;
    padding-right: 27px;
    padding-left: 43px;
  }
  &__item {
    position: relative;
    padding: 70px 30px;
    color: #fff;
    background-size: cover;
    text-align: center;
    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: '';
    }
    &_first {
      margin-top: 35px;
      background-color: #b345dc;
      &:after {
        background: url('../media/content/b-styles/bg-1.png') no-repeat;
        background-size: cover;
      }
    }
    &_second {
      &:after {
        background: url('../media/content/b-styles/bg-2.png') no-repeat;
        background-size: cover;
      }
    }
  }
  &__item-title {
    position: relative;
    z-index: 1;
    margin-bottom: 10px;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 700;
    line-height: 1.2;
  }
  &__item-info {
    position: relative;
    z-index: 1;
  }
}
