.b-phone {
  margin-top: 28px;
  color: #222;
  .ic {
    display: inline-block;
    margin-right: 10px;
    font-size: 20px;
    transform: rotate(-90deg);
    vertical-align: middle;
  }
  &__number {
    display: inline-block;
    margin-left: 10px;
    padding: 13px 20px;
    font-size: 18px;
    font-weight: 900;
    line-height: 1;
    color: #222;
    background-color: #f2f6f7;
    vertical-align: middle;
  }

  &_mod-a {
    display: inline-block;
    margin-left: 15px;
    padding: 4px 28px;
    vertical-align: 10%;
    .ic {
      margin-right: 0;
    }
  }
  &_mod-a & {
    &__number {
      margin-left: 0;
      padding-right: 0;
      padding-left: 15px;
    }
  }
}
