.section-sidebar {
  margin-bottom: 45px;
  .form-control {
    background-color: #FFF;
  }
}
.widget_mod-a {
  .widget-title {
    height: 58px;
    .ic {
      height: 100%;
      padding-left: 10px;
      padding-right: 10px;
      clip-path: polygon(0 0, 60% 0, 100% 100%, 0% 100%);
    }
  }
  .widget-inner {
    padding: 33px 0 0;
  }
}

.widget-title {
  position: relative;
  overflow: hidden;
  margin-bottom: 0;
  color: #fff;
  font: 700 18px/1 Montserrat;
  padding: 20px 0 14px 26px;
  background-color: #222222;
  box-shadow: 0 5px 35px 5px rgba(0, 0, 0, 0.12);
  .ic {
    font-size: 39px;
    font-weight: 400;
    margin-right: 20px;
    color: #fff;
    clip-path: polygon(0 0, 86% 0, 100% 100%, 0% 100%);
    padding: 9px 16px;
  }
  &:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    border: 7px solid transparent;
  }
}

.widget-title-2 {
  position: relative;
  overflow: hidden;
  margin-bottom: 0;
  color: #222222;
  font: 700 18px/1 Montserrat;
  padding-top: 25px;
  .ic {
    font-size: 39px;
    font-weight: 400;
    color: #fff;
    clip-path: polygon(0 0, 86% 0, 100% 100%, 0% 100%);
    padding: 9px 16px;
  }
  &:after {
    content: '';
    position: absolute;
    top: 5px;
    right: 5px;
    border: 7px solid transparent;
  }
}

.widget-inner {
  padding: 33px 26px 50px;
}

.widget-2 {
  margin-bottom: 35px;
  .widget-inner {
    padding: 28px 26px 35px;
  }
  .btn {
    font-size: 14px;
    font-weight: 700;
  }
  .form-control:not(textarea) {
    height: 50px;
    padding-left: 18px;
    padding-right: 18px;
  }
  .form-group {
    margin-bottom: 15px;
  }
}


.widget-list {
  max-width: 250px;
  margin-bottom: 0;
  padding-top: 0;
  margin-right: 0;

  &__item {}
  &__link {
    display: block;
    font-size: 18px;
    color: #222;
    padding: 2px 10px 15px 22px;
    border-bottom: 1px solid #ddd;
    transition: all .3s;
  }
}
.widget-list {
  & &__link {
    color: $color-dark;
  }
  & &__item {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}


.post-widget {
  padding-top: 12px;
  padding-bottom: 25px;
  border-bottom: 1px solid #ddd;
  &__media {
    float: left;
    width: 80px;
  }
  &__inner {
    padding-left: 105px;
  }
  &__title {
    display: block;
    padding-top: 7px;
    color: #222222;
    font-size: 18px;
    font-weight: 400;
    a {
      color: $color-dark;
    }
  }
  &__date {
    font-size: 12px;
    color: #555;
  }
}


.list-tags {
  margin-right: -4px;
  margin-bottom: -8px;
  margin-left: -4px;

  @include clearfix;
  &__item {
    float: left;
    margin-right: 4px;
    margin-bottom: 8px;
    margin-left: 4px;
  }
  &__link {
    display: block;
    padding: 8px 15px;
    font-family: Montserrat;
    font-size: 13px;
    color: $color-dark;
    border: 1px solid #ddd;
    border-radius: 2px;
    background-color: #fff;
    transition: all .3s;
    &:hover {
      color: #fff;
      text-decoration: none;
    }
  }
}


.widget-card {
  background-color: #f4f4f4;
  padding-bottom: 10px;
  .btn {
    display: block;
    margin: 15px 10px 0;
  }
}

.widget-card-number {
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 10px;
  margin-bottom: 20px;
  &__item {
    padding: 20px 0;
    background-color: #ffffff;
    text-align: center;
    font-size: 22px;
    font-weight: 700;
    margin-left: 5px;
    margin-right: 5px;
  }
  &__info {
    color: #222222;
    font-size: 18px;
    font-weight: 400;
  }
}

.widget-card-descr {
  &__item {
    padding: 16px 35px;
    &:not(:first-child) {
      border-top: 1px solid #ddd;
    }
  }
  &__title {
    color: #222222;
    font-size: 18px;
    font-weight: 700;
  }
  &__info {
    color: #555555;
    font-size: 15px;
    font-weight: 400;
  }
}


.widget-rates {
  &__title {
    text-align: center;
    color: #222222;
    font-size: 18px;
    font-weight: 700;
    padding-top: 30px;
    padding-bottom: 18px;
  }
  &__table {
    table {
      width: 100%;
    }
    td {
      color: #555555;
      font-size: 15px;
      font-weight: 400;
      padding-left: 34px;
      padding-right: 34px;
      line-height: 1.6;
      &:last-child {
        text-align: right;
        font-weight: 700;
      }
    }
  }
  &__note {
    margin-top: 36px;
    color: #555555;
    font-size: 12px;
    font-weight: 400;
    border-top: 1px solid #ddd;
    padding: 25px 43px;
  }
}

.widget-rates {
  padding-bottom: 10px;
  .btn {
    margin-left: 10px;
    margin-right: 10px;
    display: block;
  }
}

.widget-rates-info {
  background-color: #ffffff;
  padding: 20px  10px 25px 20px;
  margin: 0 10px 10px;
  &__text {
    color: #555555;
    font-size: 15px;
    padding-left: 25px;
    line-height: 1.47;
  }
  .ic {
    font-size: 40px;
  }
}
